"use client"
import React, { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'

type WizardState = {
  voice: 'Soprano' | 'Alto' | 'Tenor' | 'Bass' | ''
  availability: string
  experience: 'Beginner' | 'Intermediate' | 'Advanced' | ''
  sightReading: 'Comfortable' | 'Somewhat' | 'Uncomfortable' | ''
}

const initial: WizardState = { voice: '', availability: '', experience: '', sightReading: '' }

export default function AuditionWizard() {
  const [step, setStep] = useState(0)
  const [state, setState] = useState<WizardState>(() => {
    try { return JSON.parse(localStorage.getItem('audition-wizard') || 'null') || initial } catch { return initial }
  })
  const [complete, setComplete] = useState(false)
  const router = useRouter()

  useEffect(() => { localStorage.setItem('audition-wizard', JSON.stringify(state)) }, [state])

  const next = () => setStep((s) => Math.min(3, s + 1))
  const prev = () => setStep((s) => Math.max(0, s - 1))

  const onFinish = () => {
    setComplete(true)
    localStorage.setItem('audition-wizard-complete', JSON.stringify(state))
  }

  const schedule = () => {
    const summary = `Voice: ${state.voice}; Availability: ${state.availability}; Experience: ${state.experience}; Sight-reading: ${state.sightReading}`
    const url = `/contact?wizard=true&summary=${encodeURIComponent(summary)}#schedule`
    router.push(url)
  }

  if (complete) {
    return (
      <div className="border p-4 rounded border-neutral-800">
        <h3 className="font-semibold">Your Audition Fit</h3>
        <p className="mt-2 text-neutral-300">Based on your responses:</p>
        <ul className="list-disc ml-6 mt-2 text-neutral-300">
          <li>What to Prepare: A short excerpt in your comfortable range, 45-90 seconds.</li>
          <li>What to Expect: A vocal warm-up, a short prepared piece, and a sight-reading check.</li>
        </ul>
        <div className="mt-4 flex gap-2">
          <button className="px-3 py-2 rounded bg-neutral-700" onClick={schedule}>Schedule Audition</button>
          <button className="px-3 py-2 rounded border border-neutral-700" onClick={() => { setComplete(false); setStep(0) }}>Edit</button>
        </div>
      </div>
    )
  }

  return (
    <div className="border p-4 rounded border-neutral-800">
      <div className="mb-3 text-sm text-neutral-400">Step {step + 1}/4</div>
      {step === 0 && (
        <div>
          <label className="block font-medium">Voice Part</label>
          <div className="mt-2 flex gap-2">
            {['Soprano', 'Alto', 'Tenor', 'Bass'].map((v) => (
              <button key={v} className={`px-3 py-2 rounded ${state.voice === v ? 'bg-neutral-700' : 'border border-neutral-700'}`} onClick={() => setState({ ...state, voice: v as WizardState['voice'] })}>{v}</button>
            ))}
          </div>
        </div>
      )}
      {step === 1 && (
        <div>
          <label className="block font-medium">Rehearsal Availability</label>
          <div className="mt-2 flex gap-2 flex-wrap">
            {['Weeknight', 'Weekend Daytime', 'Flexible', 'Other'].map((v) => (
              <button key={v} className={`px-3 py-2 rounded ${state.availability === v ? 'bg-neutral-700' : 'border border-neutral-700'}`} onClick={() => setState({ ...state, availability: v })}>{v}</button>
            ))}
          </div>
        </div>
      )}
      {step === 2 && (
        <div>
          <label className="block font-medium">Experience Level</label>
          <div className="mt-2 flex gap-2">
            {['Beginner', 'Intermediate', 'Advanced'].map((v) => (
              <button key={v} className={`px-3 py-2 rounded ${state.experience === v ? 'bg-neutral-700' : 'border border-neutral-700'}`} onClick={() => setState({ ...state, experience: v as WizardState['experience'] })}>{v}</button>
            ))}
          </div>
        </div>
      )}
      {step === 3 && (
        <div>
          <label className="block font-medium">Sight-Reading Comfort</label>
          <div className="mt-2 flex gap-2">
            {['Comfortable', 'Somewhat', 'Uncomfortable'].map((v) => (
              <button key={v} className={`px-3 py-2 rounded ${state.sightReading === v ? 'bg-neutral-700' : 'border border-neutral-700'}`} onClick={() => setState({ ...state, sightReading: v as WizardState['sightReading'] })}>{v}</button>
            ))}
          </div>
        </div>
      )}

      <div className="mt-4 flex gap-2">
        <button className="px-3 py-2 rounded border" onClick={prev} disabled={step === 0}>Back</button>
        {step < 3 ? (
          <button className="px-3 py-2 rounded bg-neutral-700" onClick={next}>Next</button>
        ) : (
          <button className="px-3 py-2 rounded bg-neutral-700" onClick={onFinish}>Finish</button>
        )}
      </div>
    </div>
  )
}
